"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { Trash2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Separator } from "@/components/ui/separator";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import CheckoutButton from "./Checkout_button";
import NumberInput from "./Numberinput";

// Sample cart data - in a real app, this would come from your context or API
const initialCartItems = [
  {
    id: 1,
    name: "Wool Blend Coat",
    price: 129.99,
    quantity: 3,
    image: "/placeholder.svg?height=120&width=100",
    color: "Beige",
    size: "M",
  },
  {
    id: 2,
    name: "Cashmere Sweater",
    price: 89.99,
    quantity: 4,
    image: "/placeholder.svg?height=120&width=100",
    color: "Gray",
    size: "L",
  },
];

export default function CartPage() {
  const [cartItems, setCartItems] = useState([]);
  const [selectedItems, setSelectedItems] = useState({});
  const [itemQuantities, setItemQuantities] = useState({});
  const [location, setLocation] = useState("");
  const [phoneNo, setPhoneNo] = useState("");
  const [isLoaded, setIsLoaded] = useState(false);

  // Initialize cart on client-side only
  useEffect(() => {
    setCartItems(initialCartItems);

    // Initialize quantities
    const initialQuantities = {};
    initialCartItems.forEach((item) => {
      initialQuantities[item.id] = item.quantity;
    });
    setItemQuantities(initialQuantities);

    setIsLoaded(true);
  }, []);

  // Handle checkbox selection
  const handleCheckboxChange = (itemId, baseAmount) => (checked) => {
    setSelectedItems((prev) => {
      const newSelectedItems = { ...prev };
      const existingQuantity = itemQuantities[itemId] || 1;

      if (checked) {
        newSelectedItems[itemId] = baseAmount * existingQuantity;
      } else {
        delete newSelectedItems[itemId];
      }

      return newSelectedItems;
    });
  };

  // Update total for item when quantity changes
  const updateTotalForItem = (itemId, totalPrice, quantity) => {
    setItemQuantities((prev) => ({ ...prev, [itemId]: quantity }));

    setSelectedItems((prev) => {
      if (prev[itemId] !== undefined) {
        return { ...prev, [itemId]: totalPrice };
      }
      return prev;
    });
  };

  // Handle item removal
  const handleRemoveItem = (itemId) => {
    setCartItems((prev) => prev.filter((item) => item.id !== itemId));

    // Also remove from selected items if it was selected
    setSelectedItems((prev) => {
      const newSelectedItems = { ...prev };
      delete newSelectedItems[itemId];
      return newSelectedItems;
    });
  };

  // Calculate totals
  const subtotal = Object.values(selectedItems).reduce(
    (sum, price) => sum + price,
    0
  );
  const shipping = subtotal > 0 ? 12.99 : 0;
  const total = subtotal + shipping;

  if (!isLoaded) {
    return (
      <main className="min-h-screen py-12">
        <div className="container mx-auto px-4 text-center">
          Loading cart...
        </div>
      </main>
    );
  }

  return (
    <main className="min-h-screen py-12">
      <div className="container mx-auto px-4">
        <h1 className="text-3xl font-serif mb-8">Shopping Cart</h1>

        {cartItems.length > 0 ? (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-2">
              <div className="bg-white rounded-lg shadow-sm p-6">
                <div className="hidden md:grid md:grid-cols-12 text-sm font-medium text-muted-foreground mb-4">
                  <div className="md:col-span-1"></div> {/* Checkbox column */}
                  <div className="md:col-span-5">Product</div>
                  <div className="text-center md:col-span-2">Price</div>
                  <div className="text-center md:col-span-2">Quantity</div>
                  <div className="text-right md:col-span-2">Total</div>
                </div>
                <Separator className="mb-6 hidden md:block" />

                {cartItems.map((item) => {
                  const itemTotal =
                    item.price * (itemQuantities[item.id] || item.quantity);

                  return (
                    <div key={item.id} className="mb-6">
                      <div className="grid grid-cols-1 md:grid-cols-12 gap-4 items-center">
                        <div className="md:col-span-1 flex justify-center">
                          <Checkbox
                            id={`select-${item.id}`}
                            onCheckedChange={handleCheckboxChange(
                              item.id,
                              item.price
                            )}
                          />
                        </div>
                        <div className="md:col-span-5 flex items-center gap-4">
                          <div className="relative h-24 w-20 rounded overflow-hidden bg-muted/20 flex-shrink-0">
                            <Image
                              src={item.image || "/placeholder.svg"}
                              alt={item.name}
                              fill
                              className="object-cover"
                            />
                          </div>
                          <div>
                            <h3 className="font-medium">{item.name}</h3>
                            <p className="text-sm text-muted-foreground">
                              Color: {item.color}
                            </p>
                            <p className="text-sm text-muted-foreground">
                              Size: {item.size}
                            </p>
                          </div>
                        </div>
                        <div className="text-center md:col-span-2">
                          ${item.price.toFixed(2)}
                        </div>
                        <div className="flex items-center justify-center md:col-span-2">
                          <NumberInput
                            baseAmount={item.price}
                            initialQuantity={item.quantity}
                            onTotalChange={(total, quantity) =>
                              updateTotalForItem(item.id, total, quantity)
                            }
                          />
                        </div>
                        <div className="flex items-center justify-between md:justify-end md:col-span-2">
                          <span className="md:hidden">Total:</span>
                          <span className="font-medium">
                            ${itemTotal.toFixed(2)}
                          </span>
                        </div>
                      </div>
                      <div className="flex justify-end mt-2">
                        <button
                          className="text-sm text-red-500 flex items-center gap-1"
                          onClick={() => handleRemoveItem(item.id)}
                        >
                          <Trash2 className="h-4 w-4" /> Remove
                        </button>
                      </div>
                      <Separator className="my-6" />
                    </div>
                  );
                })}

                <div className="flex justify-between items-center">
                  <Link href="/" className="text-sm font-medium underline">
                    Continue Shopping
                  </Link>
                  <Button variant="outline">Update Cart</Button>
                </div>
              </div>
            </div>

            <div className="lg:col-span-1">
              <div className="bg-white rounded-lg shadow-sm p-6">
                <h2 className="text-lg font-medium mb-4">Order Summary</h2>

                <div className="space-y-4 mb-6">
                  <div>
                    <Label
                      htmlFor="location"
                      className="text-sm font-medium mb-1 block"
                    >
                      Delivery Location
                    </Label>
                    <Input
                      id="location"
                      placeholder="Enter your address"
                      value={location}
                      onChange={(e) => setLocation(e.target.value)}
                    />
                  </div>

                  <div>
                    <Label
                      htmlFor="phoneNo"
                      className="text-sm font-medium mb-1 block"
                    >
                      Phone Number
                    </Label>
                    <Input
                      id="phoneNo"
                      placeholder="Enter your phone number"
                      value={phoneNo}
                      onChange={(e) => setPhoneNo(e.target.value)}
                    />
                  </div>
                </div>

                <Separator className="my-4" />

                <div className="space-y-3 text-sm">
                  <div className="flex justify-between">
                    <span className="text-muted-foreground">Subtotal</span>
                    <span>${subtotal.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-muted-foreground">Shipping</span>
                    <span>${shipping.toFixed(2)}</span>
                  </div>
                  <Separator className="my-3" />
                  <div className="flex justify-between font-medium text-base">
                    <span>Total</span>
                    <span>${total.toFixed(2)}</span>
                  </div>
                </div>

                <div className="mt-6">
                  <CheckoutButton
                    selectedItems={selectedItems}
                    itemQuantities={itemQuantities}
                    location={location}
                    phoneno={phoneNo}
                  />
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="text-center py-12">
            <h2 className="text-2xl font-medium mb-4">Your cart is empty</h2>
            <p className="text-muted-foreground mb-8">
              Looks like you haven't added any items to your cart yet.
            </p>
            <Link href="/">
              <Button className="bg-black hover:bg-gray-800 text-white">
                Continue Shopping
              </Button>
            </Link>
          </div>
        )}
      </div>
    </main>
  );
}
